import { useState, useEffect, useMemo, useRef } from 'react';
import './Memory.css'

const EMOJI_POOL = [
    '', '', '', '', '', '', '', '',
    '', '', '', '', '', '', '', '',
    '', '', '', '', '', '', '', '',
    '', '', '', '', '', '', '', '',
    '', '', '', '', '', '', '', '',
    '', '', '', '', '', '', '', ''
];

function shuffle(array) {
    const a = array.slice();
    for (let i = a.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [a[i], a[j]] = [a[j], a[i]];
    }
    return a;
}

const Memory = () => {
    const [rows, setRows] = useState(4);
    const [cols, setCols] = useState(4);
    const [hardMode, setHardMode] = useState(false);
    const [timeLimit, setTimeLimit] = useState(90);
    const [timeLeft, setTimeLeft] = useState(timeLimit);

    const totalCards = rows * cols;
    const totalPairs = Math.floor(totalCards / 2);

    const [deck, setDeck] = useState([]);
    const [firstPick, setFirstPick] = useState(null);
    const [secondPick, setSecondPick] = useState(null);
    const [busy, setBusy] = useState(false);
    const [moves, setMoves] = useState(0);
    const [matches, setMatches] = useState(0);
    const [gameOver, setGameOver] = useState(false);
    const [victory, setVictory] = useState(false);
    const timerRef = useRef(null);

    const bestKey = useMemo(() => `memory_best_${rows}x${cols}${hardMode ? '_hard' : ''}`, [rows, cols, hardMode]);
    const [bestMoves, setBestMoves] = useState(() => Number(localStorage.getItem(bestKey) || 0));

    const initDeck = () => {
        const chosen = shuffle(EMOJI_POOL).slice(0, totalPairs);
        const base = shuffle([...chosen, ...chosen]).slice(0, totalCards);
        const withIds = base.map((v, i) => ({
            id: i + '-' + Math.random().toString(36).slice(2, 7),
            value: v,
            flipped: false,
            matched: false
        }));
        setDeck(withIds);
        setFirstPick(null);
        setSecondPick(null);
        setMoves(0);
        setMatches(0);
        setGameOver(false);
        setVictory(false);
        setBusy(false);
        setTimeLeft(timeLimit);
        if (timerRef.current) clearInterval(timerRef.current);
        if (hardMode) startTimer();
    };

    const startTimer = () => {
        timerRef.current = setInterval(() => {
            setTimeLeft(t => {
                if (t <= 1) {
                    clearInterval(timerRef.current);
                    setGameOver(true);
                    setVictory(false);
                    return 0;
                }
                return t - 1;
            });
        }, 1000);
    };

    useEffect(() => {
        setBestMoves(Number(localStorage.getItem(bestKey) || 0));
    }, [bestKey]);

    useEffect(() => {
        initDeck();
    }, [rows, cols, hardMode, timeLimit]);

    const handleFlip = (card) => {
        if (busy || gameOver || victory) return;
        if (card.flipped || card.matched) return;

        setDeck(prev => prev.map(c => c.id === card.id ? { ...c, flipped: true } : c));

        if (!firstPick) {
            setFirstPick(card);
            return;
        }
        if (firstPick && !secondPick) {
            setSecondPick(card);
            setBusy(true);
            setMoves(m => m + 1);

            setTimeout(() => {
                setDeck(prev => {
                    const a = prev.find(c => c.id === firstPick.id) || firstPick;
                    const b = prev.find(c => c.id === card.id) || card;
                    if (a.value === b.value) {
                        setMatches(mt => mt + 1);
                        return prev.map(c =>
                            c.value === a.value ? { ...c, matched: true } : c
                        );
                    } else {
                        return prev.map(c =>
                            (c.id === a.id || c.id === b.id) ? { ...c, flipped: false } : c
                        );
                    }
                });
                setFirstPick(null);
                setSecondPick(null);
                setBusy(false);
            }, 700);
        }
    };

    useEffect(() => {
        if (matches === totalPairs && totalPairs > 0) {
            setVictory(true);
            setGameOver(false);
            if (hardMode && timerRef.current) clearInterval(timerRef.current);
            if (bestMoves === 0 || moves < bestMoves) {
                setBestMoves(moves);
                localStorage.setItem(bestKey, String(moves));
            }
        }
    }, [matches, totalPairs, moves, bestMoves, bestKey, hardMode]);

    const resetGame = () => initDeck();

    const gridStyle = {
        gridTemplateColumns: `repeat(${cols}, minmax(0, 1fr))`
    };

    return (
        <div className="memory-game">
            <div className="game-container">
                {/* Header */}
                <div className="game-header">
                    <h1> Memory Game</h1>
                    <p>Lt th tm cp ging nhau. Hon thnh vi t lt nht!</p>
                </div>

                <div className="game-layout">
                    {/* Board */}
                    <div className="game-board">
                        <div className="board-card">
                            {/* Stats */}
                            <div className="stats-container">
                                <div className="stats-group">
                                    <div className="stat moves">
                                         Lt: {moves}
                                    </div>
                                    <div className="stat matches">
                                         Cp: {matches}/{totalPairs}
                                    </div>
                                    {hardMode && (
                                        <div className={`stat timer ${timeLeft <= 10 ? 'critical' : ''}`}>
                                             Thi gian: {timeLeft}s
                                        </div>
                                    )}
                                </div>
                                <div className="action-buttons">
                                    <button onClick={resetGame} className="btn reset"> Chi mi</button>
                                </div>
                            </div>

                            {/* Grid */}
                            <div className="cards-grid" style={gridStyle}>
                                {deck.map((card) => {
                                    const stateClass = card.matched ? 'matched' : card.flipped ? 'flipped' : '';
                                    return (
                                        <button
                                            key={card.id}
                                            className={`memory-card ${stateClass}`}
                                            onClick={() => handleFlip(card)}
                                            aria-label={card.flipped || card.matched ? `Th ${card.value}` : 'Lt th'}
                                        >
                                            <div className="card-inner">
                                                <div className="card-face front">?</div>
                                                <div className="card-face back">{card.value}</div>
                                            </div>
                                        </button>
                                    );
                                })}
                            </div>

                            {/* Result */}
                            {(victory || gameOver) && (
                                <div className="result-message">
                                    {victory ? (
                                        <div className="victory">
                                            <div className="title"> Tuyt vi! Bn  hon thnh.</div>
                                            <div className="subtitle">Tng lt: {moves}{bestMoves === moves ? '  K lc mi!' : ''}</div>
                                        </div>
                                    ) : (
                                        <div className="game-over">
                                            <div className="title"> Ht thi gian!</div>
                                            <div className="subtitle">Th li vi chin thut khc nh.</div>
                                        </div>
                                    )}
                                </div>
                            )}
                        </div>
                    </div>

                    {/* Side panel */}
                    <div className="side-panel">
                        <div className="settings-panel">
                            <div className="panel-title">Ci t</div>
                            <div className="settings-content">
                                <div className="setting-group">
                                    <label>S hng</label>
                                    <select
                                        value={rows}
                                        onChange={(e) => setRows(Number(e.target.value))}
                                    >
                                        {[2, 3, 4, 5, 6].map(r => <option key={r} value={r}>{r}</option>)}
                                    </select>
                                </div>
                                <div className="setting-group">
                                    <label>S ct</label>
                                    <select
                                        value={cols}
                                        onChange={(e) => setCols(Number(e.target.value))}
                                    >
                                        {[2, 3, 4, 5, 6].map(c => <option key={c} value={c}>{c}</option>)}
                                    </select>
                                </div>
                                <div className="setting-group toggle">
                                    <div>
                                        <div className="label">Ch  kh</div>
                                        <div className="description">m ngc thi gian</div>
                                    </div>
                                    <label className="toggle-switch">
                                        <input type="checkbox" checked={hardMode} onChange={(e) => setHardMode(e.target.checked)} />
                                        <span className="slider"></span>
                                    </label>
                                </div>
                                {hardMode && (
                                    <div className="setting-group">
                                        <label>Thi gian (giy)</label>
                                        <input
                                            type="range" min="30" max="180" step="10"
                                            value={timeLimit}
                                            onChange={(e) => setTimeLimit(Number(e.target.value))}
                                        />
                                        <div className="time-display">Gii hn: {timeLimit}s</div>
                                    </div>
                                )}
                                <div className="action-buttons">
                                    <button onClick={resetGame} className="btn reset"> Chi mi</button>
                                    <button onClick={initDeck} className="btn shuffle"> Xo bi</button>
                                </div>
                            </div>
                        </div>

                        <div className="best-score-panel">
                            <div className="panel-title">K lc</div>
                            <div className="score">Li {rows}{cols}{hardMode ? '  Kh' : ''}: {bestMoves || ''} lt</div>
                        </div>

                        <div className="tips-panel">
                            <div className="panel-title">Mo</div>
                            <ul className="tips-list">
                                <li>Quan st theo nhm, ghi nh v tr theo hng/ct.</li>
                                <li>Lt nhanh hai th u  xc nh mc so snh.</li>
                                <li>Cng t lt cng tt  ng vi nu khng chc chn.</li>
                            </ul>
                        </div>
                    </div>
                </div>

                <div className="game-footer">
                    Gi : Li chn x chn s lun cn i cp th.
                </div>
            </div>
        </div>
    );
};

export default Memory;